.ShipTray-container {
    width: 15%;
    height: 0;
    padding-top: 15%;
    position: relative;
    margin: auto;
    display: inline-block;
}

.ShipTray {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: grid;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    background: black;
    grid-gap: 1px;
    border: 1px solid black;
}


